<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-form :model=\&quot;setting\&quot; direction=\&quot;horizontal\&quot;&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot;&gt;\n            &lt;v-switch v-model=\&quot;setting.autoplay\&quot;&gt;\n                &lt;span slot=\&quot;checkedChildren\&quot;&gt;&lt;/span&gt;\n                &lt;span slot=\&quot;unCheckedChildren\&quot;&gt;&lt;/span&gt;\n            &lt;/v-switch&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot;&gt;\n            &lt;v-radio-group type=\&quot;button\&quot; v-model=\&quot;setting.dots\&quot; :data=\&quot;[{value: 'inside', text: ''},{value: 'outside', text: ''},{value: 'none', text: ''}]\&quot;&gt;\n            &lt;/v-radio-group&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot;&gt;\n             &lt;v-radio-group type=\&quot;button\&quot; v-model=\&quot;setting.arrow\&quot; :data=\&quot;[{value: 'hover', text: ''},{value: 'always', text: ''},{value: 'never', text: ''}]\&quot;&gt;\n            &lt;/v-radio-group&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot;&gt;\n            &lt;v-radio-group type=\&quot;button\&quot; v-model=\&quot;setting.trigger\&quot; :data=\&quot;[{value: 'click', text: ''},{value: 'hover', text: ''}]\&quot;&gt;\n            &lt;/v-radio-group&gt;\n        &lt;/v-form-item&gt;\n    &lt;/v-form&gt;\n    &lt;v-carousel v-model=\&quot;value2\&quot; :autoplay=\&quot;setting.autoplay\&quot; :autoplay-speed=\&quot;setting.autoplaySpeed\&quot; :dots=\&quot;setting.dots\&quot; :trigger=\&quot;setting.trigger\&quot; :arrow=\&quot;setting.arrow\&quot;&gt;\n        &lt;v-carousel-item&gt;\n            &lt;div class=\&quot;demo-carousel\&quot;&gt;1&lt;/div&gt;\n        &lt;/v-carousel-item&gt;\n        &lt;v-carousel-item&gt;\n            &lt;div class=\&quot;demo-carousel\&quot;&gt;2&lt;/div&gt;\n        &lt;/v-carousel-item&gt;\n        &lt;v-carousel-item&gt;\n            &lt;div class=\&quot;demo-carousel\&quot;&gt;3&lt;/div&gt;\n        &lt;/v-carousel-item&gt;\n        &lt;v-carousel-item&gt;\n            &lt;div class=\&quot;demo-carousel\&quot;&gt;4&lt;/div&gt;\n        &lt;/v-carousel-item&gt;\n    &lt;/v-carousel&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    export default { \n        data () {\n            return {\n                value2: 0,\n                setting: {\n                    autoplay: false,\n                    autoplaySpeed: 2000,\n                    dots: 'inside',\n                    trigger: 'click',\n                    arrow: 'hover'\n                },\n                labelCol: {\n                    span: 6\n                },\n                wrapperCol: {\n                    span: 14\n                }\n            }\n        },\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-form :model="setting" direction="horizontal">
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol">
            <v-switch v-model="setting.autoplay">
                <span slot="checkedChildren"></span>
                <span slot="unCheckedChildren"></span>
            </v-switch>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol">
            <v-radio-group type="button" v-model="setting.dots" :data="[{value: 'inside', text: ''},{value: 'outside', text: ''},{value: 'none', text: ''}]">
            </v-radio-group>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol">
             <v-radio-group type="button" v-model="setting.arrow" :data="[{value: 'hover', text: ''},{value: 'always', text: ''},{value: 'never', text: ''}]">
            </v-radio-group>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol">
            <v-radio-group type="button" v-model="setting.trigger" :data="[{value: 'click', text: ''},{value: 'hover', text: ''}]">
            </v-radio-group>
        </v-form-item>
    </v-form>
